'use client';

import { useState } from 'react';
import Link from 'next/link';

export default function NotificationsPage() {
    const [notifications] = useState([
        {
            id: '1',
            title: 'Delivery Confirmed',
            message: 'Your package has been successfully delivered.',
            time: '2m ago',
            type: 'success',
            icon: 'âœ…'
        },
        {
            id: '2',
            title: 'Payment Received',
            message: 'â‚¬ 34.00 (To Picker) has been added to your wallet.',
            time: '1h ago',
            type: 'payment',
            icon: 'ðŸ’°'
        },
        {
            id: '3',
            title: 'New Message',
            message: 'You have a new message from Anna P.',
            time: '3h ago',
            type: 'message',
            icon: 'ðŸ’¬'
        },
        {
            id: '4',
            title: 'Package Picked Up',
            message: 'Your package has been picked up by John A.',
            time: '1:15 PM',
            type: 'pickup',
            icon: 'ðŸ“¦',
            isYesterday: true
        }
    ]);

    const getIconColor = (type: string) => {
        switch (type) {
            case 'success':
                return 'bg-green-500';
            case 'payment':
                return 'bg-orange-500';
            case 'message':
                return 'bg-blue-500';
            case 'pickup':
                return 'bg-purple-500';
            default:
                return 'bg-gray-500';
        }
    };

    return (
        <div className="min-h-screen bg-black text-white flex flex-col">
            {/* Status Bar */}
            <div className="flex justify-between items-center p-4 pt-12 text-sm">
                <span>9:41</span>
                <div className="flex items-center gap-1">
                    <div className="flex gap-1">
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M2 17h20v2H2v-2zm1.15-6.05L4 9.47l.85 1.48H5c.55 0 1 .45 1 1s-.45 1-1 1-.45-.45-.45-1zm5.5 2.5H9c.55 0 1-.45 1-1s-.45-1-1-1-.45.45-.45 1z" />
                    </svg>
                    <div className="w-6 h-3 border border-white rounded-sm">
                        <div className="w-4 h-1.5 bg-white rounded-sm m-0.5"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2Z" />
                    </svg>
                </div>
            </div>

            {/* Header */}
            <div className="flex items-center px-8 mb-8">
                <Link href="/profile" className="mr-4">
                    <svg className="w-6 h-6 text-white" viewBox="0 0 24 24" fill="currentColor">
                        <path d="M15 18l-6-6 6-6" />
                    </svg>
                </Link>
                <h1 className="text-3xl font-bold">Notifications</h1>
            </div>

            {/* Notifications */}
            <div className="flex-1 px-8">
                {/* Today Section */}
                <div className="mb-6">
                    <h2 className="text-lg font-semibold text-gray-400 mb-4">Today</h2>
                    <div className="space-y-4">
                        {notifications.filter(n => !n.isYesterday).map((notification) => (
                            <div key={notification.id} className="flex items-start space-x-3 bg-gray-900 rounded-lg p-4">
                                <div className={`w-10 h-10 ${getIconColor(notification.type)} rounded-full flex items-center justify-center flex-shrink-0`}>
                                    <span className="text-white text-sm">{notification.icon}</span>
                                </div>
                                <div className="flex-1 min-w-0">
                                    <h3 className="text-white font-medium mb-1">{notification.title}</h3>
                                    <p className="text-gray-400 text-sm mb-2">{notification.message}</p>
                                    <span className="text-gray-500 text-xs">{notification.time}</span>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Yesterday Section */}
                <div className="mb-6">
                    <h2 className="text-lg font-semibold text-gray-400 mb-4">Yesterday</h2>
                    <div className="space-y-4">
                        {notifications.filter(n => n.isYesterday).map((notification) => (
                            <div key={notification.id} className="flex items-start space-x-3 bg-gray-900 rounded-lg p-4">
                                <div className={`w-10 h-10 ${getIconColor(notification.type)} rounded-full flex items-center justify-center flex-shrink-0`}>
                                    <span className="text-white text-sm">{notification.icon}</span>
                                </div>
                                <div className="flex-1 min-w-0">
                                    <h3 className="text-white font-medium mb-1">{notification.title}</h3>
                                    <p className="text-gray-400 text-sm mb-2">{notification.message}</p>
                                    <span className="text-gray-500 text-xs">{notification.time}</span>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>

            {/* Bottom Indicator */}
            <div className="flex justify-center pb-6">
                <div className="w-32 h-1 bg-white rounded-full"></div>
            </div>
        </div>
    );
} 